.popup__error {
  font-size: 12px;
  line-height: 15px;
  color: #ff0000;
  margin: 1px 0 0 0;
  min-height: 30px;
  max-width: 350px;
}

@media screen and (max-width: 550px) {
  .popup__error {
    max-width: 240px;
    font-size: 10px;
    line-height: 13px;
    min-height: 26px;
  }
}
